import { FC } from 'react'
import NavBar from "../components/NavBar";
import {Tool} from "../interfaces/tools";
import {Link} from "react-router-dom";
import {getToolRoute} from "../utils/routeUtils";
import {TOOLS} from "../constants/config";

const ToolsPage: FC = () => {
    return (
        <>
            <NavBar />
            <div className="px-20 mt-20">
                <div className="grid gap-y-10 grid-cols-1 lg:grid-cols-2 xl:grid-cols-3 justify-items-center">
                    {TOOLS.map(item => (
                        <ToolCard tool={item} key={item.id} />
                    ))}
                </div>
            </div>
        </>
    );
}

interface IToolCardProps {
    tool: Tool
}

const ToolCard: FC<IToolCardProps> = ({ tool }) => {
    return (
        <>
            <Link to={getToolRoute(tool)}>
                <div className="w-80 h-36 px-10 py-8 shadow rounded-lg transform hover:scale-105 transition text-black">
                    <p className="text-lg font-bold">{tool.name}</p>
                    <p className="">{tool.tip}</p>
                </div>
            </Link>
        </>
    );
}

export default ToolsPage;
