import {createRef, FC, RefObject, useState} from 'react'
import NavBar from "../../components/NavBar";
import {Button, UploadProps} from "antd";
import PageTitle from "../../components/PageTitle";
import {Image, Layer, Line, Stage} from "react-konva";
import {Vector2d} from "konva/lib/types";
import DragUpload from "../../components/base/DragUpload";
import {VIDEOS} from "../../constants/acceptedTypes";

const SubtitleRecognitionPage: FC = () => {
    const [video, setVideo] = useState<string>();
    const [uploaded, setUploaded] = useState(false);
    const uploadProps: UploadProps = {
        name: 'file',
        action: '#',
        accept: VIDEOS,
        showUploadList: false,
        beforeUpload() {
            return false;
        },
        onChange(info) {
            const {status} = info.file
            console.log(status)
            if (status !== 'uploading') {
                console.log(info.file.size)
                console.log(info.file, info.fileList);
                handleUploaded()
            }
            setVideo(URL.createObjectURL(info.file))
        }
    }
    const handleUploaded = () => {
        setUploaded(true)
    }

    return (
        <>
            <NavBar/>
            <div className="px-20">
                <PageTitle title="Â≠óÂπïËØÜÂà´" subtitle="‰ªéËßÜÈ¢ë‰∏≠ÊèêÂèñÂ≠óÂπïÔºåËæìÂá∫ srt Êñá‰ª∂"/>
                {!uploaded && <DragUpload {...uploadProps} />}
                {uploaded && <div className="mt-20">
                  <Editor video={video!}/>
                </div>}
            </div>
        </>
    );
}

interface IEditorProps {
    video: string
}

const Editor: FC<IEditorProps> = (props) => {
    const imageRef: RefObject<any> = createRef();
    const videoRef: RefObject<HTMLVideoElement> = createRef();
    const [size, setSize] = useState({ width: 50, height: 50 });
    const [playing, setPlaying] = useState(false);

    const [, setMaxHeight] = useState(0);
    const [, setMinHeight] = useState(0);

    const [, setMaxWidth] = useState(0);
    const [, setMinWidth] = useState(0);

    const [defaultLines, setDefaultLines] = useState({
        minHeight: 10,
        minWidth: 10,
        maxHeight: 0,
        maxWidth: 0
    });

    const verticalDragBoundFunc = (pos: Vector2d) => {
        return {
            x: 0,
            y: pos.y
        }
    }
    const horizontalDragBoundFunc = (pos: Vector2d) => {
        return {
            x: pos.x,
            y: 0
        }
    }

    return (
        <>
            <div className="flex justify-center mt-10">
                <video className={'absolute'} ref={videoRef} src={props.video} onLoadedMetadata={() => {
                    setSize({
                        width: videoRef.current!.videoWidth,
                        height: videoRef.current!.videoHeight
                    });

                    setMinHeight(10)
                    setMinWidth(10)
                    setMaxHeight(videoRef.current!.videoHeight - 10)
                    setMaxWidth(videoRef.current!.videoWidth - 10)

                    setDefaultLines({
                        ...defaultLines,
                        maxHeight: videoRef.current!.videoHeight - 10,
                        maxWidth: videoRef.current!.videoWidth - 10
                    })
                }} onEnded={() => setPlaying(false)}/>
                <Stage height={size.height} width={size.width} className="rounded-lg">
                    <Layer>
                        <Image
                            ref={imageRef}
                            image={videoRef.current!}
                            x={0}
                            y={0}
                            width={size.width}
                            height={size.height}
                        />
                        <Line points={[0, defaultLines.minHeight, size.width, defaultLines.minHeight]} tension={1} strokeWidth={8} stroke="purple"
                              draggable
                              dragBoundFunc={verticalDragBoundFunc} onDragMove={evt => {
                            setMinHeight(evt.target.y())
                        }}/>
                        <Line points={[0, defaultLines.maxHeight, size.width, defaultLines.maxHeight]} tension={1} strokeWidth={8} stroke="purple"
                              draggable
                              dragBoundFunc={verticalDragBoundFunc} onDragMove={evt => {
                            setMaxHeight(evt.target.y())
                        }}/>
                        <Line points={[defaultLines.minWidth, 0, defaultLines.minWidth, size.height]} tension={1} strokeWidth={8} stroke="purple"
                              draggable
                              dragBoundFunc={horizontalDragBoundFunc} onDragMove={evt => {
                            setMinWidth(evt.target.x())
                        }}/>
                        <Line points={[defaultLines.maxWidth, 0, defaultLines.maxWidth, size.height]} tension={1} strokeWidth={8} stroke="purple"
                              draggable
                              dragBoundFunc={horizontalDragBoundFunc} onDragMove={evt => {
                            setMaxWidth(evt.target.x())
                        }}/>
                    </Layer>
                </Stage>
            </div>
            <div className="flex justify-center items-center space-x-4">
                <Button onClick={() => {
                    if (videoRef.current!.paused) {
                        videoRef.current!.play().then(_ => _)
                    } else {
                        videoRef.current!.pause()
                    }
                    setPlaying(!videoRef.current!.paused)
                }}>{playing ? 'ÊöÇÂÅú' : 'Êí≠Êîæ'}</Button>
                <progress className="rounded-lg bg-blue-500 w-full cursor-pointer" id="progress" value={0}
                          onClick={event => {
                              let progress = document.getElementById('progress')
                              let pos = (event.pageX - progress!.offsetLeft) / progress!.offsetWidth
                              videoRef.current!.currentTime = pos * videoRef.current!.duration
                          }}>
                    <span id="progress-bar" className="bg-blue-500"/>
                </progress>
            </div>
        </>
    );
}

export default SubtitleRecognitionPage;
