import React from 'react';
import {BrowserRouter as Router, Route, Switch} from "react-router-dom";
import HomePage from "./pages/HomePage";
import {TOOLS} from "./constants/config";
import {getToolRoute} from "./utils/routeUtils";
import SubtitleRecognitionPage from "./pages/tools/SubtitleRecognitionPage";
import ToolsPage from "./pages/ToolsPage";
import SimpleToolPage from "./pages/tools/SimpleToolPage";
import {Helmet} from "react-helmet";

const App = () => {
    return (
        <>
            <Helmet>
                <title>Á±≥ÊãâËßÜÈ¢ë</title>
            </Helmet>

            <div className="container mx-auto">
                <Router>
                    <Switch>
                        <Route exact path={'/'} children={<HomePage />} />
                        <Route path={'/tools'} children={<ToolsPage />} />
                        <Route path={'/test'} children={<SubtitleRecognitionPage />} />
                        {TOOLS.map(item => (
                            (item.isSimplePage && <Route key={item.id} path={getToolRoute(item)} children={<SimpleToolPage tool={item} />} />) ||
                            (<Route key={item.id} path={getToolRoute(item)} children={item.page} />)
                        ))}
                    </Switch>
                </Router>
            </div>
        </>
  );
}

export default App;
